.TodoInput{
    width: 100%;
    background: transparent;
    border: none;
    border-bottom:1px solid #fff;
    outline: none;
    color: #ffffff;
    padding: 0 0 8px 0;
}
input::-webkit-input-placeholder{
    color: rgba(255,255,255,0.9);
}
input:-ms-input-placeholder{
    color: rgba(255,255,255,0.9);
}
input::placeholder{
    color: rgba(255,255,255,0.9);
}


